import Link from "next/link";

export default function ProductCard({ product }) {
    return (
        <div className="border p-4 rounded shadow hover:shadow-lg">
            <img src={product.image} alt={product.title} className="h-48 w-full object-cover rounded mb-2" />
            <h2 className="font-bold text-lg">{product.title}</h2>
            <p className="text-gray-600 text-sm">{product.shortDesc}</p>
            <p className="text-green-600 font-semibold mt-1">${product.price}</p>
            <Link href={`/items/${product._id}`} className="mt-2 inline-block text-sm text-green-700 hover:underline">
                Details
            </Link>
        </div>
    );
}
